@import url(https://fonts.googleapis.com/css2?family=Open+Sans);

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

body {
  position: relative;
  color: white;
  height: 100vh;

  background-image: linear-gradient(43deg,
      #4158d0 0%,
      #c850c0 46%,

      #ffcc70 100%);
  animation: bg-animation 15s ease infinite alternate;
  background-size: 400%;
  font-family: sans-serif;
  overflow-y: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Open Sans", sans-serif;
}

@keyframes bg-animation {
  0% {
    background-position: left;
  }

  100% {
    background-position: right;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.clock {
  font-size: 80px;
  user-select: none;
}

.date {
  font-size: 20px;
  user-select: none;
}

.weather {
  font-size: 20px;
  user-select: none;

}

.elements {
  text-align: center;
  justify-content: center;
  align-items: center;
}

.elements>h1 {
  margin: 5px;
}

.bottomheader {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  font-size: 20px;
  color: white;
  user-select: none;
}

.settings {
  display: flex;
  font-size: 40px;
  position: absolute;
  top: 5px;
  margin: 5px;
  left: 1%;
  user-select: none;
}

.list {
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);
}